
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  width: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.app-info {
  width: 25%;
  float: left;
}

.App-title {
  margin-top: 5px;
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.new-acronym-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0 auto;
  width: 40%;
}

.new-acronym-form .form-actions {
  text-align: right;
  margin-top: 15px;
}

.acronym-container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
  width: 100%;
}

/*.acronym-list {
  display: flex;
  margin-bottom: 10px;
}*/

.acronym-card {
  position: relative;
  margin-bottom: 10px;
}

.profile-block {
  /* width: 25%; */
  float: right;
}

.text-fields {
  margin: 25px 0;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



